export class AboutResponse {
    frontEnd: VersionItemResponse[] = [];
    backEnd: VersionItemResponse[] = [];
    database: VersionItemResponse[] = [];
    constructor(data?: any) {
        if (data === undefined) {
            this.frontEnd = [];
            this.backEnd = [];
            this.database = [];
            return;
        }

        this.frontEnd = data.frontEnd.map((data: any, index: number) => {
            return new VersionItemResponse(data);
        });
        this.backEnd = data.backEnd.map((data: any, index: number) => {
            return new VersionItemResponse(data);
        });
        this.database = data.database.map((data: any, index: number) => {
            return new VersionItemResponse(data);
        });
    }
}

export class VersionItemResponse {
    stt: number | null;
    dateRelease: string | null;
    key: string;
    message: string;
    name: string;
    timestamp: string;
    version: Version;
    type: number;
    preRelease: string;
    versionStr: string;
    constructor(data?: any, index?: number) {
        this.stt = index !== undefined ? (index + 1) : null;
        this.dateRelease = data.dateRelease;
        this.key = data.key;
        this.message = data.message;
        this.name = data.name;
        this.timestamp = data.timestamp;
        this.version = data.version;
        this.type = data.type;
        this.preRelease = data.preRelease;
        this.versionStr = data.versionStr;
    }
}

export class Version {
    major: number
    minor: number
    patch: number
    preRelease: string
    constructor(data: any) {
        this.major = data.major;
        this.minor = data.minor;
        this.patch = data.patch;
        this.preRelease = data.preRelease;
    }
}