import { Divider } from "antd";
import { VersionItemResponse } from "../../models/versionManageModel";
import { UIFormatDateTimeVNComponent } from "../core/UI_FormatComponent";

export default function AboutItem(props: any) {
    return (
        <div className="my-box">
            <h4>{props.title}</h4>
            <Divider />
            <div>
                {props.data.map((data: VersionItemResponse, index: number) => {
                    return (
                        <div key={index}>
                            <strong>Version:</strong> {data.versionStr} - <strong>Release date:</strong> 
                            <UIFormatDateTimeVNComponent value={data.dateRelease} />
                            <ul>
                                <li>{data.name}</li>
                                <li>{data.message}</li>
                            </ul>
                        </div>
                    );
                })}
            </div>
        </div>
    );
}